import {INIT_USER_INFO, AUTO_LOGIN_INFO, LOG_OUT, INIT_CART_COUNT, DEAL_CART_COUNT} from './mutations-type'
import {removeStore, setStore} from "../assets/js/global";


export default {
     // 同步用户的信息
     [INIT_USER_INFO](state, value){
         state.userInfo = value;
         // 保存用户信息到本地
         setStore('AgentUser', value);
     },

     // 自动登录
     [AUTO_LOGIN_INFO](state, value){
         state.userInfo = value;
     },

     // 退出登录
     [LOG_OUT](state){
         // 清空用户信息
         state.userInfo = {};
         // 清空本地的信息
         removeStore('agent');
     },

     // 初始化购物车商品总数量
    [INIT_CART_COUNT](state, count){
         state.cartGoodsCount = count;
    },

    // 购物车中商品的总数量加或减
    [DEAL_CART_COUNT](state, num){
        state.cartGoodsCount += num;
    }
}
