import ajax from './ajax';

// 1. 基础路径
const BASE_URL = 'http://localhost:3000';

// 2. 请求登录
export const GetLogin = (username,password) => ajax(BASE_URL + '/api/user/adminLogin',{username,password},'post');


//3、合同相关接口
// 3.1请求合同信息
export const GetTableData = (username) => ajax(BASE_URL + '/api/table/GetTableData',{username},'post');

//3.2合同搜索
export const SearchContract = (contract_id)=>ajax(BASE_URL + '/api/table/SearchContract',{contract_id},'post');

//3.3删除单项合同
export const DeleteContract = (contract_id)=>ajax(BASE_URL + '/api/table/DeleteContract',{contract_id},'post');

//3.4批量删除
export const DeleteAllSelection = (contract_obj)=>ajax(BASE_URL + '/api/table/DeleteAllSelection',{contract_obj},'post');

//3.5编辑合同
export const EditContract = (form)=>ajax(BASE_URL + '/api/table/EditContract',{form},'post');




//4、网站授权相关接口
//4.1请求网站授权信息
export const GetWebsite = (username)=> ajax(BASE_URL+'/api/website/GetWebsite',{username},'post');

//4.2合同搜索
export const SearchWebsite = (agent_name)=>ajax(BASE_URL + '/api/website/SearchWebsite',{agent_name},'post');

//4.3删除单项
export const DeleteWebsite = (agent_name)=>ajax(BASE_URL + '/api/website/DeleteWebsite',{agent_name},'post');

//4.4批量删除
export const DeleteAllWebsite = (website_obj)=>ajax(BASE_URL + '/api/website/DeleteAllWebsite',{website_obj},'post');

//4.5编辑合同
export const EditWebsite = (form)=>ajax(BASE_URL + '/api/website/EditWebsite',{form},'post');

