import {INIT_USER_INFO, AUTO_LOGIN_INFO, LOG_OUT, INIT_CART_COUNT, DEAL_CART_COUNT} from './mutations-type'
import {getStore} from "../assets/js/global";
import {getGoodsCart} from "../server";

export default {
    // 初始化用户信息
    initUserInfo({commit}, userInfo){
        commit(INIT_USER_INFO, userInfo);
    },
    // 自动登录
    autoLogin({commit}){
        // 1. 从本地去取用户数据
        const userInfo = JSON.parse(getStore('AgentUser'));
        if(userInfo !== null){
            commit(AUTO_LOGIN_INFO, userInfo);
        }
    },
    // 退出登录
    delUserInfo({commit}){
        commit(LOG_OUT);
    },

    // 初始化购物车商品总数量
    async initCartCount({commit}, user_id){
        if(user_id){
            // 1. 获取购物车中的数据
            let result = await getGoodsCart(user_id);
            // console.log(result);

            if(result.success_code === 200){
                let totalCount = 0;
                result.data.forEach((value, index)=>{
                    totalCount += value.num;
                });
                commit(INIT_CART_COUNT, totalCount);
            }
        }else {
            commit(INIT_CART_COUNT, 0);
        }
    },

    // 购物车中商品的总数量加或减
    dealCartCount({commit}, num){
        commit(DEAL_CART_COUNT, num);
    }
}
