import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: () => import(/* webpackChunkName: "login" */ './../views/Login/Login.vue'),
    meta: { title: '登录' }
  },
  {
    path: '/home',
    component: () => import(/* webpackChunkName: "home" */ './../views/Home/Home.vue'),
    redirect: '/dashboard',
    meta: { title: '自述文件' },
    children: [
      {
        path: '/dashboard',
        component: () => import(/* webpackChunkName: "dashboard" */ './../views/Dashboard/Dashboard.vue'),
        meta: { title: '系统首页' }
      },
      {
        path: '/table',
        component: () => import(/* webpackChunkName: "table" */ './../views/BaseTable/BaseTable.vue'),
        meta: { title: '合同管理' }
      },
      {
        path: '/website',
        component: () => import(/* webpackChunkName: "tabs" */ './../views/WebSite/WebSite.vue'),
        meta: { title: '网站管理' }
      },
      {
        path: '/tableUpload',
        component: () => import(/* webpackChunkName: "form" */ './../views/tableUpload/tableUpload.vue'),
        meta: { title: '合同批量上传' }
      },
      {
        path: '/websiteUpload',
        component: () => import(/* webpackChunkName: "donate" */ './../views/websiteUpload/websiteUpload.vue'),
        meta: { title: '网站信息批量上传' }
      }
    ]
  }
]

const router = new VueRouter({
  routes
})



//前置路由导航
router.beforeEach((to,from,next)=>{
  // console.log(to.path)
  if(to.path !== '/'){
    // console.log(window.isLogin);
    let token = localStorage.getItem('agent')
    // 已经登录
    if(token){
      // console.log('登录成功');
      return next();
    }
    // 没有登录
    return next('/?redirect=' + to.path);//去往登录页面并把要去的页面储存
  }
// 放行
  next();
})



export default router
