import axios from 'axios';

export default function ajax(url = '',  params = {}, type = 'get') {
    let promise;
    return new Promise((resolve, reject)=>{
        // 1. 判断请求类型
        if(type.toUpperCase() === 'GET'){ // get请求
            // 1.1 拼接参数
            let paramsStr = '';
            /*
              {
                 name: '张三',
                 age: 18,
                 sex: '男'
              }
              name=张三&age=18&sex=男&random=7dewdew

              http://www.itlike.com/api?name=张三&age=18&sex=男&random=7dewdew
            */
            Object.keys(params).forEach((key)=>{
                paramsStr += key + '=' + params[key] + '&';
            });
            // 1.2 拼接随机字符串,防止get的路径缓存
            paramsStr += 'random=' + getRandomStr();
            // 1.3 拼接完整的请求路径
            url += '?' + paramsStr;
            // 1.4 发起get请求
            promise = axios.get(url);
        }else if(type.toUpperCase() === 'POST'){ // post请求
            promise = axios.post(url, params);
        }

        // 2. 处理结果并返回
        promise.then((response)=>{
            resolve(response.data);
        }).catch((error)=>{
            reject(error);
        })
    });
}

function getRandomStr() {
    return String(Math.random()).substr(2)
}
